import {useState} from 'react'
import NavBar from '../NavBar'
import EmojiCard from '../EmojiCard'
import WinOrLoseCard from '../WinOrLoseCard'
import './index.css'

const shuffleEmojis = emojis => [...emojis].sort(() => Math.random() - 0.5)

const EmojiGame = props => {
  const {emojisList} = props

  const [clickedEmojis, setClickedEmojis] = useState([])
  const [score, setScore] = useState(0)
  const [topScore, setTopScore] = useState(0)
  const [isGameOver, setIsGameOver] = useState(false)
  const [isWon, setIsWon] = useState(false)

  const resetGame = () => {
    setClickedEmojis([])
    setScore(0)
    setIsGameOver(false)
    setIsWon(false)
  }

  const finishGame = won => {
    setIsGameOver(true)
    setIsWon(won)
    if (score > topScore) {
      setTopScore(score)
    }
  }

  const onEmojiClick = id => {
    if (clickedEmojis.includes(id)) {
      finishGame(false)
    } else {
      const newScore = score + 1
      const newClicked = [...clickedEmojis, id]
      setClickedEmojis(newClicked)
      setScore(newScore)

      if (newScore === emojisList.length) {
        finishGame(true)
      }
    }
  }

  const onPlayAgain = () => {
    if (score > topScore) {
      setTopScore(score)
    }
    resetGame()
  }

  const shuffled = shuffleEmojis(emojisList)

  return (
    <div className="emoji-game-container">
      <NavBar score={score} topScore={topScore} isGameOver={isGameOver} />
      <div className="emoji-game-body">
        {isGameOver ? (
          <WinOrLoseCard
            isWon={isWon}
            score={score}
            totalEmojis={emojisList.length}
            onPlayAgain={onPlayAgain}
          />
        ) : (
          <ul className="emojis-list">
            {shuffled.map(each => (
              <EmojiCard
                key={each.id}
                emojiDetails={each}
                onEmojiClick={onEmojiClick}
              />
            ))}
          </ul>
        )}
      </div>
    </div>
  )
}

export default EmojiGame
